//所有的接口

import ajax from './ajax'

//1.基础路径
// const BASE_URL = '/api';
 const BASE_URL = 'http://127.0.0.1:3000';

//2.请求方法
//2.1请求首页轮播图
export const getHomeCasual = () => ajax(BASE_URL + '/api/homecasual');

//2.2请求首页导航
export const getHomeNav = () => ajax(BASE_URL + '/api/homenav');

//2.3请求首页商品数据
export const getHomeShopList = () => ajax(BASE_URL + '/api/homeshoplist');

//2.4请求推荐商品数据
export const getRecommendShopList = () => ajax(BASE_URL + '/api/recommendshoplist');

//2.5请求搜索列表数据
export const getSearchGoods = () => ajax(BASE_URL + '/api/searchgoods');

//2.6请求短信验证码
export const getPhoneCode = (phone) => ajax(BASE_URL + '/api/send_code', {phone});

//2.7手机验证码登录
export const phoneCodeLogin = (phone, code) => ajax(BASE_URL + '/api/login_code', {phone, code}, 'POST');

//2.8用户名和密码登录
export const pwdLogin = (name, pwd, captcha) => ajax(BASE_URL + '/api/login_pwd', {name, pwd, captcha}, 'POST');

//2.9获取登录用户信息
export const getUserInfo = () => ajax(BASE_URL + '/api/user_info');

//2.10退出登录
export const logOut = () => ajax(BASE_URL + '/api/logout');

//2.11修改用户信息
export const changeUserInfo = (user_id,user_name, user_sex, user_address, user_birthday, user_sign) => ajax(BASE_URL + '/api/change_user_msg',user_id,user_name, user_sex, user_address, user_birthday, user_sign);




